@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,600&display=swap);
body{margin:0;font-family:"Monteserrat", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}*{box-sizing:border-box}

.credits-content{padding:10px;font-size:12px;color:#fff;text-align:center;margin:5px 20px}.credits-content a{text-decoration:none;color:#eab543}@media (max-width: 600px){.credits-content{font-size:10px;padding:0;padding-top:2px}}

.Footer{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;width:100%;background-color:#487eb0}@media (max-width: 600px){.Footer{-webkit-flex-direction:column;flex-direction:column}}

.navlink{padding:10px}.navbar-link{text-decoration:none;color:#c8d6e5}.active-page{color:#ff9f43;text-decoration:underline}

.navbar-container{width:100%;height:10%;background-color:#487eb0;margin:0;display:-webkit-flex;display:flex}.navbar-container ul{margin:0;padding:5px;background-color:#487eb0;list-style:none;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.navbar-container ul>li{font-size:28px;margin:0 20px}@media (max-width: 600px){.navbar-container ul>li{font-size:20px}}

.app-container{background-color:#40739e;width:100%;min-height:90vh;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}@media (max-width: 600px){.app-container{-webkit-flex-direction:column;flex-direction:column;overflow-y:scroll;height:100%}}

button{margin:10px;width:100px;padding:10px;border:none;border-radius:20px;color:#fff;font-weight:bold;cursor:pointer}button:active{outline:none;border:2px solid black;-webkit-transform:scale(0.8);transform:scale(0.8)}button.clicked{border:2px solid black}

.drink-container{width:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column}.drink-buttons-container{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column}.drink-container .page-title{font-weight:bold;margin:20px 0 10px 0;color:#fff;text-transform:uppercase;text-align:center;letter-spacing:1.5px;top:2%}.selected-color{font-family:"Roboto", sans-serif;color:#fff;text-align:center;letter-spacing:1px}

.cup{background-color:#fff;border:4px solid #144fc6;color:#144fc6;border-radius:0 0 40px 40px;height:330px;width:150px;margin:30px 0;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;overflow:hidden}.remainded{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;text-align:center;-webkit-flex:1 1;flex:1 1;transition:height 0.3s ease}.remainded span{font-size:32px;font-weight:bold}.remainded small{font-size:20px}.percentage{height:0px;background-color:#6ab3f8;font-size:30px;font-weight:bold;visibility:hidden;transition:height 0.3s ease;padding:15px;margin:0;width:100%}

.cup-small{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;margin:10px 8px;width:60px;height:95px;background-color:rgba(255,255,255,0.9);border:4px solid #144fc6;border-radius:0 0 25px 25px;cursor:pointer;font-size:16px;font-weight:bold;text-align:center;color:#144fc6;transition:background-color 0.3s ease;overflow:hidden}.cup-small.full{background-color:#6ab3f8;color:#fff}.cup-small:active{-webkit-transform:scale(0.88);transform:scale(0.88)}

.cups{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;width:350px;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}

.paragraph{color:#fff;text-align:center;margin:0 0 20px;font-size:inherit}

.homepage-container{height:90vh;width:100%;background-color:#40739e;margin:0;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}.homepage-container>.page-title{font-weight:bold;margin:0;padding:10px;color:#fff;text-align:center;letter-spacing:1.5px;font-size:58px}.homepage-container>.homepage-space{height:30%}.homepage-container>.homepage-paragraph{width:400px}@media (max-width: 600px){.homepage-container>.homepage-paragraph{width:100%}}

